<template>
  <v-container fill-height>
    <v-row justify="center" align="center">
      <v-col lg="6">
        <v-card>
          <v-card-text>
            <v-container>
              <form @submit.prevent="createPost">
                <v-row>
                  <v-col xs12>
                    <v-text-field
                      name="title"
                      label="Title"
                      id="title"
                      v-model="title"
                      type="title"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col xs12>
                    <v-text-field
                      name="description"
                      label="Description"
                      id="description"
                      v-model="description"
                      type="description"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col xs12>
                    <v-file-input v-model="inputFile" show-size label="File input"></v-file-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col xs12>
                    <v-btn type="submit">Create Post</v-btn>
                  </v-col>
                </v-row>
              </form>
            </v-container>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      title: "",
      description: "",
      inputFile: {}
    };
  },
  methods: {
    ...mapActions(["newPost"]),
    createPost() {
      let data = {
        title: this.title,
        description: this.description,
      };
      data = JSON.stringify(data);
      let formData = new FormData();
      formData.append("file", this.inputFile);
      formData.append("data", data);
      this.$store
        .dispatch("newPost", formData)
        .then(() => this.$router.push("/"))
        .catch(err => console.log(err));
    }
  }
};
</script>