<template>
  <div>
    <v-container>
      <v-row justify="center">
        <v-col lg="6">
          <v-row>
            <v-col>
              <v-card>
                <v-card-title class="headline">{{selected.title}}</v-card-title>
                <v-img :src="'./' + selected.image"></v-img>
                <v-card-subtitle v-text="selected.description"></v-card-subtitle>
                <v-card-actions>
                  <v-btn color="orange" text>
                    {{selected.comments.length}}
                    <v-icon right dark>mdi-comment</v-icon>
                  </v-btn>
                  <v-btn color="orange" @click="upvote" text>
                    {{selected.voters.length}}
                    <v-icon right dark>mdi-arrow-up-bold-box</v-icon>
                  </v-btn>
                  <v-btn
                    color="orange"
                    @click="remove"
                    text
                    v-if="selected.author == userLogged.id"
                  >
                    <v-icon dark>mdi-trash-can-outline</v-icon>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-card>
                <v-container>
                  <form @submit.prevent="createComment">
                    <v-row>
                      <v-col cols="12">
                        <v-textarea
                          class="mx-2"
                          label="Add a new comment"
                          rows="1"
                          name="content"
                          id="content"
                          type="content"
                          v-model="content"
                          prepend-icon="mdi-comment"
                        ></v-textarea>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col>
                        <v-btn type="submit">Comment</v-btn>
                      </v-col>
                    </v-row>
                  </form>
                </v-container>
              </v-card>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-card v-for="(comment,index) in selected.comments" :key="index" class="mt-2">
                <v-container>
                  <h1>{{comment.content}}</h1>
                  <v-card-actions>
                    <v-btn color="orange" @click="upvoteComment(comment._id)" text>
                      {{comment.voters.length}}
                      <v-icon right dark>mdi-arrow-up-bold-box</v-icon>
                    </v-btn>
                    <v-btn
                      color="orange"
                      @click="removeComment(comment._id)"
                      text
                      v-if="comment.author == userLogged.id"
                    >
                      <v-icon dark>mdi-trash-can-outline</v-icon>
                    </v-btn>
                  </v-card-actions>
                </v-container>
              </v-card>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      content: ""
    };
  },
  computed: mapGetters(["isAuthenticated", "selected", "userLogged"]),
  methods: {
    ...mapActions([
      "fetchPost",
      "newComment",
      "upvotePost",
      "deletePost",
      "upComment",
      "deleteComment"
    ]),
    createComment() {
      this.newComment(this.content);
    },
    upvote() {
      this.upvotePost(this.selected._id);
    },
    remove() {
      this.deletePost(this.selected._id).then(() => {
        this.$router.push("/");
      });
    },
    upvoteComment(id) {
      this.upComment(id);
    },
    removeComment(id) {
      this.deleteComment(id);
    }
  },
  created() {
    this.fetchPost(this.$route.params.postId);
  }
};
</script>

<style scoped></style>